import { Component, OnInit } from '@angular/core';
declare var $: any;
@Component({
  selector: 'aboutcontent',
  templateUrl: './aboutcontent.component.html',
  styleUrls: ['./aboutcontent.component.css'],
  
})
export class AboutcontentComponent implements OnInit {

  constructor() { }

 ngOnInit() {
$(".select").selectmenu();
$(".select__nationality").select2();
$(".radio").checkboxradio();
$('#scroll').perfectScrollbar({
	suppressScrollX: true
  });
  $('#mobileScroll01').perfectScrollbar({
	suppressScrollX: true
  });
$( function() {
$( "#tabs" ).tabs();
 $('.tabs__content_scroll').perfectScrollbar({
		suppressScrollX: true
	});
  } );
  }

}
