import { Component, OnInit } from '@angular/core';
import { PodpiskaComponent } from '../podpiska/podpiska.component';
declare var $: any;
@Component({
  selector: 'frends',
  templateUrl: './frends.component.html',
  styleUrls: ['./frends.component.css']
})
export class FrendsComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  function heightDetect() {
		$(".section").css("height", $(window).height());
	};
	heightDetect();
	$(window).resize(function() {
		heightDetect();
	});
	$('#scroll').perfectScrollbar({
		suppressScrollX: true
	});
	$('#scroll01').perfectScrollbar({
		suppressScrollX: true
	});
	$('.carousel-partners').bxSlider({
		minSlides: 3,
		moveSlides: 1,
		slideMargin: 10,
		onSliderLoad: function () {
		    $('.carousel-partners>div:not(.bx-clone)').eq(1).addClass('active-slide');
		},
		onSlideAfter: function ($slideElement, oldIndex, newIndex) {
		    $('.carousel__item').removeClass('active-slide');
		    $($slideElement).next().addClass('active-slide');        
		}
	});
  $(document).ready(function(){
	$(".toggleMenu").on("click", function(e){
		$(".slideoutMenu").toggleClass("collapseMenu");
	});
	$('#scroll03').perfectScrollbar({
		suppressScrollX: true
	});
	$('.scrollableContent').perfectScrollbar({
		suppressScrollX: true
	});
});
  }

}



