
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Router } from '@angular/router';

import './rxjs-operators';
import { Observable } from "rxjs/Rx";

@Injectable()
export class CityService
{
    private _citiesUrl = '/admin/content-api/get?slug=';

    constructor(private http: Http, private router: Router) {}

    public getAll() : Observable<Object[]> {
        var slug = this.router.url; 
        
        slug = slug.substr(1);
        this._citiesUrl += slug;
        console.log(this._citiesUrl);
        console.info(slug);
        
        //var res = this.http.get(this._citiesUrl).map((res: Response) => res.json());
        return this.http.get(this._citiesUrl).map(function(res: Response){ 
            /*console.log(res.json());*/ 
            console.log(res);
            return res.json()
        });
        
    }
}


