import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IndexComponent } from './index/index.component';
import { AboutComponent } from './about/about.component';
import { ProductComponent } from './product/product.component';
import { FrendsComponent } from './frends/frends.component';
import { OffersComponent } from './offers/offers.component';
import { ProgramComponent } from './program/program.component';
import { AbouthistoryComponent } from './about/aboutcontent/abouthistory/abouthistory.component';
import { AboutcontactComponent } from './about/aboutcontent/aboutcontact/aboutcontact.component';
import { AboutpartnerComponent } from './about/aboutcontent/aboutpartner/aboutpartner.component';

export const router: Routes = [
    {path: '', redirectTo: 'index', pathMatch: 'full'},
    {path: 'index', component: IndexComponent},
    {path: 'about', component: AboutComponent},
    {path: 'product', component: ProductComponent},
    {path: 'frends', component: FrendsComponent},
    {path: 'offers', component: OffersComponent},
    {path: 'program', component: ProgramComponent},
    {path: 'abouthistory', component: AbouthistoryComponent},
    {path: 'aboutcontact', component: AboutcontactComponent},
    {path: 'aboutpartner', component: AboutpartnerComponent},
];

export const routes: ModuleWithProviders = RouterModule.forRoot(router);
